.b-info {
  padding-bottom: rem(100);
  overflow: hidden;
  &__container {
    @include flex(false, space-between);
    @include media(sm) {
      display: block;
      padding: 0;
    }
  }

  &__card {
    background: #F5F5F5;
    padding: rem(53) 0 rem(47) rem(50);
    display: block;
    width: auto;
    height: auto;
    @include media(sm) {
      padding: 0;
      display: flex;
    }
  }

  &__form {
    @include size(50%, auto);
    @include media(sm) {
      width: 100%;
      max-width: 768px;
    }
  }

  &__item {
    @include flex;
    @include margin(57, bottom);
    background-color: #f5f5f5;

    &:last-child {
      @include margin(0, bottom);
    }

    @include media(sm) {
      display: block;
      text-align: center;
      padding: rem(43) 0;
      margin-bottom: 0;
      height: auto;
    }
  }

  &__icon {
    @include margin(24, right);

    svg {
      @include size(rem(60))
    }

    @include media(sm) {
      margin: 0;
    }
  }

  &__text {
    @include font(18);
    line-height: rem(30);

    span {
      display: block;
      @include media(sm) {
        display: inline;
      }
    }

    @include media(sm) {
      width: rem(250);
      display: block;
      @include mx--auto;
    }
  }

  &__form {
    color: $light;
    background: url("../img/info/bg.jpg");
    padding: rem(62) rem(95) rem(60) rem(110);
    @include media(sm) {
      padding: rem(60) 0;
      margin-top: rem(55);
      background-size: cover;
      background-repeat: no-repeat;
      display: block;
    }
  }

  &__title {
    text-align: center;
    @include font(26, $bold);
    font-family: $accentFont;
    @include margin(28, bottom);
    @include media(sm) {
      text-align: center;
    }
  }

  &__row {
    @include media(sm) {
      display: block;
      width: 350px;
      @include mx--auto;
      padding-right: 12px;
      padding-left: 12px;
      max-width: 100%;
      input, button {
        max-width: 100%;
      }
    }
  }

  &__input, &__button {
    @include margin(30, bottom);
    display: block;
  }

  .swiper-container {
    width: 100%;
  }

  @include media(sm) {
    padding: rem(0);
    padding-bottom: rem(52);
  }
  @include media(xs) {
    .swiper-pagination-bullet {
      width: 70px;
    }
  }
}
