.b-burger {
  border: none;
  background: transparent;
  box-shadow: none;
  @include size(rem(34), rem(28));
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
  span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    transform: rotate(0);
    transition: .25s ease-in-out;
    opacity: 1;
    left: 0;
    background: $text;

    &:nth-child(1) {
      top: 0;
      transform-origin: center center;
    }

    &:nth-child(2) {
      top: 50%;
      transform-origin: left center;
    }

    &:nth-child(3) {
      top: 100%;
      transform-origin: center center;
    }
  }

  &--active {
    span {
      &:nth-child(1) {
        transform: translate(0, -50%) rotate(45deg);
        top: 50%;
      }

      &:nth-child(2) {
        width: 0;
        opacity: 0;
      }

      &:nth-child(3) {
        transform: translate(0, -50%) rotate(-45deg);
        top: 50%;
      }
    }
  }
}
