.b-steps {
  padding: rem(132) 0 rem(120);
  background: url("../img/steps/bg.jpg");
  color: $light;
  text-align: center;

  &__title {
    @extend %title--big;
    @include margin(10, bottom);
    @include media(sm) {
      @include font(30);
    }
  }

  &__descruption {
    @include font(18);
    line-height: rem(20);
    @include media(sm) {
      @include font(22);
    }
  }

  &__items {
    @include flex(center, space-between);
    @include margin-y(30);
  }

  &__item {
    @include size(rem(350), auto);
    text-align: center;
    &:last-child {
      .b-steps__icon::before {
        display: none;
      }
    }
    @include media(sm) {
      text-align: left;
    }
  }

  &__icon {
    background: url("../img/steps/icon.svg") no-repeat center center/cover;
    @include size(rem(100));
    @include flex(center, center);
    @include mx--auto;
    position: relative;
    span {
      @include font(40);
      line-height: rem(20);
      color: $accent;
    }

    &::before {
      @include pseudo;
      @include size(rem(220), rem(15));
      @include align(absolute, true);
      right: rem(-250);
      background: url("../img/steps/arrow.svg") no-repeat center center/cover;
    }

    @include media(xs) {
      margin-left: 60px;
      &::before {
        right: -180px;
        width: 150px;
        height: 100px;
        background-size: contain;
      }
    }
    @include media(xsm) {
      &::before {
        right: -160px;
      }
    }
  }

  &__text {
    @include margin(17, top);
    @include font(16);
    line-height: rem(20);
    @include media(sm) {
      max-width: 230px;
      text-align: left;
      @include mx--auto;
      padding-bottom: rem(37);
    }
   }

  &__button {
    @include mx--auto;
    @include media(sm) {
      margin-top: rem(38);
    }
  }
  @include media(sm) {
    padding: rem(50) 0;
  }
  @include media(xsm) {
    .swiper-pagination-bullet {
      width: 80px;
    }
  }
}
