html {
  font-size: 16px;
  @include media(sm) {
    font-size: 16px !important;
  }
}
section {
  overflow: hidden;
}
body {
  font-family: $mainFont;
  font-size: rem(16);
  color: $text;
  overflow-x: hidden;
}

a {
  text-decoration: none !important;
  transition: color .3s;
  color: inherit;
}

* {
  outline: none;
  box-sizing: border-box;
}

img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

textarea {
  resize: none;
}
