.b-popup {
  opacity: 0;
  visibility: hidden;
  //transition: .3s;

  &--active {
    opacity: 1;
    visibility: visible;
  }

  &__overlay {
    width: 100%;
    max-width: 100vw;
    height: 100%;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    background: rgba($text, .9);
    cursor: pointer;
  }

  &--send {

  }

  &__content {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 11;
    height: auto;
    background: $light;
    width: rem(540);
    padding: rem(42) rem(95) rem(50);
    max-width: 100%;
    &--payment {
      width: rem(790);
      padding: rem(42) rem(20) rem(50);
      .b-checkbox {
        width: rem(350);
      }
      @include media(sm) {
        width: auto;
      }
    }
    @include media(sm) {
      padding-left: 5%;
      padding-right: 5%;
    }
  }

  &__close {
    background: #E5E5E5;
    width: rem(40);
    height: rem(40);
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: .3s;

    svg path{
      stroke: $text;
    }

    &:hover {
      background: $accent;
      svg path {
        stroke: $light;
      }
    }

  }
  &__row {
    @include flex(center, space-between);
    @include media(sm) {
      display: block;
    }
  }
  &__title {
    text-align: center;
    @extend %title--medium;
    margin-bottom: rem(38);
  }

  &__subtitle {
    text-align: center;
    @include font(16, 600);
    margin-bottom: rem(38);
  }

  &__form {
    height: auto;
  }

  &__input, .select2-container, &__select {
    @include size(rem(350), rem(60));
    margin-bottom: rem(30);
    display: block;
    border: 1px solid #C4C4C4;
    @include media(xs) {
      width: 100% !important;
    }
  }
  .select2-container {
    width: rem(350) !important;
  }
  &__button {
    margin: rem(20) 0;

    span {
      @include font(23);
    }
    @include media(xs) {
      width: 100%;
    }
  }
}
