.b-garanty {
  padding: rem(100) 0 rem(80);
  &__container {
    @include flex;
  }
  &__img, &__form {
    width: 50%
  }
  &__img {
    @include size(rem(540), rem(680));
    @include media(sm) {
      display: none;
    }
  }
  &__form {
    padding: rem(78) 0 0 rem(30);
    @include media(sm) {
      width: 100%;
      text-align: center;
      padding-left: 0;
      padding-top: 40px;
    }
  }
  &__title {
    @extend %title--big;
    line-height: rem(50);
    @include media(xs) {
      font-size: 30px;
      line-height: 40px;
    }
  }
  &__text {
    @include font(16);
    line-height: rem(20);
    @include margin-y(20);
    max-width: rem(350);
    letter-spacing: -.5px;
    @include media(sm) {
      max-width: none;
      text-align: left;
      margin-left: 25px;
    }
  }
  &__input {
    @include border(1, $lighten)

  }
  &__input, &__button {
    @include margin(20, bottom);
    @include media(sm) {
      display: block;
    }
  }
  &__checkbox {
    a {
      display: block;
      @include media(sm) {
        display: inline;
      }
    }
  }
  &__row {
    @include media(sm) {
      @include mx--auto;
      max-width: 350px;
    }
  }

  @include media(sm) {
    text-align: center;
    padding: rem(48) 0 rem(60);
  }
  @include media(xs) {
    padding: 20px 0 40;
  }
}
