.b-rating {
  padding: rem(45) 0 rem(73);

  &__title {
    @extend %title--big;
    line-height: rem(50);
    @include margin(50, bottom);
    max-width: 70%;
    @include media(xs) {
      max-width: none;
      @include font(30);
      line-height: normal;
    }
  }
  &__container {
    max-width: rem(730);
    position: relative;
  }
  &__img {
    position: absolute;
    @include size(rem(390), rem(590));
    right: -60%;
    top: -50px;
    @include media(sm) {
      display: none;
    }
  }
  &__row {
    @include flex;
    padding-bottom: rem(25);
    @include border(1, $lighten, bottom);
    @include media(xs) {
      display: block;
    }
  }
  &__item {
    @include margin(45, right);
    @include flex;
    &:last-child {
      margin-right: 0;
    }
    @include media(xs) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
  &__icon {
    @include size(rem(28), rem(22));
    @include margin(12, right);
    &:last-child {
      margin-right: 0;
    }
  }
  &__text {
    @include font(20, $bold, true);
    line-height: rem(20);
  }
  &__description {
    @include font(14);
    @include margin(15, top);
  }
  &__send {
    text-align: center;
    @include flex(center);
    @include font(18);
    line-height: rem(20);
    @include margin(30, top);
    a {
      @include size(rem(40));
      @include margin-x(10);
    }
    &--mobile {
      display: none;
    }
    @include media(xs) {
      display: none;
      &--mobile {
        display: block;
        span {
          display: block;
          margin-bottom: 10px;
        }
      }
    }
  }
  &__small {
    display: block;
    @include margin(20, top);
    text-align: center;
  }
  &__phone {
    @include font(50, $bold, true);
    font-family: $accentFont;
    color: $main;
    text-align: center;
    display: block;
    @include margin(10, top);
    @include media(xs) {
      @include font(35);
    }
  }
}

