.b-sales-slider {
  padding: rem(72) 0 rem(90);
  background: url("../img/salesSlider/bg.jpg") no-repeat center center/cover;
  position: relative;
  color: $light;
  &__title {
    @extend %title--big;
    @include margin(58, bottom);
    text-align: center;
  }
  &__item {
    position: relative;
    height: rem(540);
    @include media(sm) {
      height: rem(450);
    }
    @include media(xs) {
      height: rem(360);
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transition: .3s;

  }
  &__ticket {
    @include size(rem(445), rem(120));
    @include flex(center, center);
    background: $accent;
    position: absolute;
    left: rem(-20);
    top: rem(-20);
    opacity: 0;
    transition: .3s;
    span {
      @extend %title--big;
    }
    @include media(sm) {
      width: rem(300);
      height: rem(100);
      span {
        @include font(30)
      }
    }
    @include media(xs) {
      top: 35%;
    }
  }
  &__profit {
    @include size(rem(440), rem(120));
    padding: rem(20) rem(25);
    background: #000;
    @include font(29);
    line-height: rem(35);
    opacity: 0;
    position: absolute;
    bottom: rem(20);
    right: rem(-20px);
    transition: .3s;
    span {
      color: $accent;
      display: block;
    }
    @include media(sm) {
      right: -30px;
      bottom: 100px;
      width: 320px;
      height: auto;
      line-height: normal;
      @include font(20)
    }
    @include media(xs) {
      bottom: 65px;
      width: auto;
      padding: 10px 40px;
    }
  }
  .swiper-container {
    padding: rem(20) rem(70);
    position: static;
    @include media(xs) {
      position: relative;
      padding: rem(20) 12px;
    }
  }
  .swiper-slide-active {
    .b-sales-slider__ticket {
      opacity: 1;
    }
    .b-sales-slider__img {
      opacity: 1;
    }
    .b-sales-slider__profit {
      opacity: 1;
    }
  }
  .swiper-button-prev {
    left: 20%;
  }
  .swiper-button-next {
    right: 20%;
  }
  .swiper-pagination {
    display: none;
    @include media(xs) {
      display: block;
    }
  }
  @include media(sm) {
    padding: 40px 0 0;
  }
}
