.b-video {
  padding: rem(81) 0 rem(90);
  text-align: center;
  background: url("../img/video/bg.jpg");
  color: $light;
  &__title {
    @extend %title--big;
    @include margin(40, bottom);
    @include media(sm) {
      @include font(30);
    }
  }
  &__link {
    @include mx--auto;
    @include size(rem(255), rem(60));
    @include flex(center, space-between);
    &:hover {
      .b-video__icon {
        background-color: lighten(#CC2A2A, 5);
      }
      .b-video__text {
        background-color: lighten($accent, 5);
      }
    }
  }
  &__icon {
    background-color: #CC2A2A;
    @include size(rem(65), rem(60));
    @include flex(center, center);
    transition: .3s;
  }
  &__text {
    background: $accent;
    @include size(rem(190), rem(60));
    @include flex(center, center);
    transition: .3s;
  }
}
