.b-button {
  @include flex(center, center);
  border-radius: 0;
  transition: all .3s;
  border: none;
  box-shadow: none;
  background: $accent;
  cursor: pointer;
  @extend %shadow;
  &:hover {
    background: lighten($accent, 10);
  }
  &__content {
    color: $light;
    @include font(18, 600);
  }
  &--sm {
    @include size(rem(220), rem(60))
  }
  &--md {
    @include size(rem(258), rem(60))
  }
  &--xmd {
    @include size(rem(285), rem(60))
  }
  &--lg {
    @include size(rem(300), rem(60))
  }

  &--xl {
    @include size(rem(350), rem(80));
    span {
      @include font(22, 600);
    }
  }
  &--xsl {
    @include size(rem(350), rem(60));
    span {
      @include font(22, 600);
    }
  }
  @include media(xsm) {
    width: 100%;
    max-width: 300px;
    display: block;
    span {
      display: block;
      text-align: center;
      @include mx--auto;
    }
  }
}
