.b-logo {
  max-width: rem(248);
  max-height: rem(57);
  z-index: 5;
  &__info {
    @include flex(center);
  }
  &__img {
    @include media(sm) {
      margin-top: rem(10);
    }
  }
  &__mark {
    @include flex(center, center);
    @include size(rem(60), rem(25));
    @include font(10, $medium, true);
    background: $accent;
    color: $light;
    font-family: $accentFont;
  }
  &__text {
    @include font(12);
    margin-left: rem(15);
    @include media(sm) {
      @include font(10);
      margin-left: rem(8);

    }
  }
  @include media(sm) {
    max-width: rem(215);
    max-height: none;
    height: rem(65);
  }
}
