.b-header {
  padding: rem(30) 0 rem(25);
  &__container {
    @include flex(center, space-between);
  }
  &__contacts {
    text-align: right;
    @include media(sm) {
      display: none;
    }
  }
  &__phone {
    display: block;
    @include font(22, $bold);
    color: $accent;
    transition: .3s;
    align-self: flex-end;
    transform: translateY(rem(10));
    &--large {
      @include font(32);
    }
    &:hover {
      color: rgba($accent, .8);
    }

  }
  &__nav {
    @include margin(15, left);
    @include media(sm) {
      margin: 0;
    }
  }
  &__call {
    @include font(12);
    text-decoration: underline !important;
    transition: .3s;
    &:hover {
      color: $accent;
    }
  }
  &__mobile {
    display: none;
    @include media(sm) {
     @include flex(center);
     @include z(dropdown);
    }
  }
  &__icon {
    @include font(30);
    @include margin(26, right);
    color: $accent;
  }
  &__button {
    display: none;
    @include media(sm) {
      z-index: 10;
      position: relative;
      display: flex;
      @include size(rem(350), rem(40));
      max-width: none;
      @include border(1, $accent);
      background: $light;
      margin-left: auto;
      margin-right: 9px;
      span {
        @include font(14, 600);
        color: $accent;
      }
    }

  }
  @include media(sm) {
    margin: rem(20) 0;
    padding: 0;
    height: rem(65);
  }
}
