$fontPath: '../fonts/';
@font-face {
  font-family: 'Open Sans';
  src: url('#{$fontPath}OpenSans-Regular/OpenSans-Regular.eot');
  src: url('#{$fontPath}OpenSans-Regular/OpenSans-Regular.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}OpenSans-Regular/OpenSans-Regular.woff') format('woff'),
  url('#{$fontPath}OpenSans-Regular/OpenSans-Regular.ttf') format('truetype'),
  url('#{$fontPath}OpenSans-Regular/OpenSans-Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto';
  src: url('#{$fontPath}RobotoMedium/RobotoMedium.eot');
  src: url('#{$fontPath}RobotoMedium/RobotoMedium.eot?#iefix') format('embedded-opentype'),
  url('#{$fontPath}RobotoMedium/RobotoMedium.woff') format('woff'),
  url('#{$fontPath}RobotoMedium/RobotoMedium.ttf') format('truetype'),
  url('#{$fontPath}RobotoMedium/RobotoMedium.svg') format('svg');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Open Sans';
  src: url('#{$fontPath}RobotoBold/RobotoBold.eot');
  src: local('â˜º'), url('#{$fontPath}RobotoBold/RobotoBold.woff') format('woff'), url('#{$fontPath}RobotoBold/RobotoBold.ttf') format('truetype'), url('#{$fontPath}RobotoBold/RobotoBold.svg') format('svg');
  speak: none;
  font-style: normal;
  font-weight: bold;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}
