.b-cars {
  padding: rem(75) 0 rem(100);

  &__slide {
    position: relative;
    text-align: center;
    @include mx--auto;
    @include media(sm) {
      margin: 0;
      width: 100% !important;
    }
    @include media(xs) {
      padding-top: 10px;
    }
  }

  &__img {
    @include margin(40, top);
    @include media(xs) {
      margin-top: 120px;
      max-width: 346px;
      @include mx--auto;
    }
  }

  &__tooltip {
    background: #EFEFEF;
    padding: rem(25) 0 rem(19) rem(30);
    @include align(absolute);
    @include size(rem(250), rem(200));
    @extend %shadow;
    transition: .3s;
    text-align: left;
    top: 0;
    left: 20%;
    @include media(sm) {
      left: 30%;
    }
    @include media(xs) {
      left: 40%;
    }
  }

  &__name {
    @include font(30, $bold, true);
    line-height: rem(20);
  }

  &__text {
    @include font(16, 600);
    @include margin-y(10);

    span {
      @include font(16, normal);
    }
  }

  &__price {
    @include font(16);
    line-height: rem(30);

    span {
      @include font(24, $bold);
      display: block;
      color: #A6A6A6;
    }

    @include media(sm) {
      display: block;
    }
  }

  &__footer {
    @include flex;
    text-align: left;
    @include media(sm) {
      display: block;
      text-align: center;
      margin-top: -20px;
    }
  }

  &__offer {
    @include font(18, $bold);
    color: #A6A6A6;
    line-height: rem(20);

    span {
      display: block;
      @include font(14, normal);
      color: #363636;
    }

    @include margin(48, right);
    @include media(sm) {
      margin-right: 0;
    }
  }

  &__rect {
    @include size(rem(230), rem(80));
    background: $main;
    padding: rem(10) rem(18);
    color: $light;
    text-align: right;
    @include media(sm) {
      display: block;
      margin: 20px auto 0;
      text-align: center;
      line-height: rem(35);
    }
    @include media(xsm) {
      line-height: 25px;
    }
  }

  &__description {
    @include font(17, $bold, true);
  }

  &__count {
    @include font(34, $bold, true);
    display: block;
  }

  .swiper-container {
    @include media(xs) {
      padding-bottom: 60px;
    }
  }

  .swiper-slide {
    .b-cars__tooltip, .b-cars__footer {
      visibility: hidden;
      opacity: 0;
    }

    .b-cars__img {
      opacity: .4;
      filter: grayscale(100%);
    }

    &-active {
      .b-cars__tooltip, .b-cars__footer {
        visibility: visible;
        opacity: 1;
      }

      .b-cars__img {
        opacity: 1;
        filter: grayscale(0);
      }
    }

    &-next {
      transition: .3s;
      transform: translateX(50%);
    }

    &-prev {
      transition: .3s;
      transform: translateX(-50%);
    }

    &-prev, &-next {
      @include media(sm) {
        transform: none;
      }
    }
  }

  .swiper-button-prev {
    left: 25%;

  }

  .swiper-button-next {
    right: 25%;
  }

  .swiper-pagination {
    display: none;
    @include media(sm) {
      display: block;
      bottom: 0;
    }
  }

  @include media(sm) {
    padding: 50px 0 60px;
  }
}
