.noUi {
  &-connects {
    border-radius: 0;
  }

  &-target {
    box-shadow: none;
    border: none;
    background-color: $lightGrey;
    cursor: pointer;
  }
  &-connect {
    background: $main;
  }

  &-horizontal &-handle {
    @include size(rem(30));
    top: rem(-10);
    cursor: pointer;
  }
  &-value-large {
    &:last-child {
      left: 94% !important;
    }
  }
  &-handle {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.55);
    @extend %circle;
    border: none;
    background: $main;

    &::before, &::after {
      display: none;
    }
  }
  &-pips {
    z-index: 0;
    &-horizontal {
      padding: rem(20) 0;
      @include media(sm) {
        height: auto;
      }
    }
  }

  &-marker-normal {
    display: none;
  }
  &-value-large {
    color: $text;
    @include font(14);
    @include margin(5, top);
  }
}
