.b-nav {
  &__button {
    display: none;
    @include media(sm) {
      display: flex;
      @include size(345px, 60px);
      z-index: 10;
      margin-left: auto;
      margin-right: 18px;
    }
    @include media(xsm) {
      margin-top: auto;
      max-width: none;
      margin-bottom: 32px;
    }
  }
  @include media(sm) {
    overflow: hidden;
    position: absolute;
    background: $light;
    width: calc(100% + 18px);
    min-height: 100vh;
    left: -10000%;
    top: 0;
    padding-top: 105px;
    transition: .3s;
    display: flex;
    flex-direction: column;
    @include z(dropdown);
    &--active {
      left: -18px;
    }
  }
}
