.b-call-block {
  padding: rem(60)  0 rem(70);
  background: url("../img/callBlock/bg.jpg");
  text-align: center;
  &__title {
    @extend %title--big;
    @include media(xs) {
      font-size: 30px;
    }
  }
  &__subtitle {
    @include font(22);
    line-height: rem(20);
    @include margin(20, top);
    display: block;
    @include media(xs) {
      max-width: 80%;
      @include mx--auto;
    }
  }
  &__link {
    @include font(50, $bold, true);
    font-family: $accentFont;
    color: $main;
    @include margin-y(25);
    display: block;
    @include media(xs) {
      @include font(36)
    }
  }
  &__button {
    @include mx--auto;
  }
  @include media(sm) {
    padding: rem(50)  0 rem(60);
  }
}
