.b-main {
  color: $light;
  text-align: center;

  &--sales {
    padding: rem(120) 0 rem(90);
    @include media(sm) {
      max-height: 800px !important;
    }
    @include media(xs) {
      max-height: 100% !important;
      padding: 100px 12px 80px;
    }
  }

  &__title {
    @include font(72, $bold, true);
    @include margin(28, bottom);
    @include media(sm) {
      @include font(50);
      @include margin(38, bottom);
      display: block;
      text-align: center;
      @include mx--auto;
    }
  }

  &__subtitle {
    @include font(18);
    @include margin(40, bottom);
  }

  &__form {
    .b-form__input, .select2-container {
      width: rem(230) !important;
    }
  }

  &__row {
    filter: drop-shadow(0 10px 20px rgba(0, 0, 0, .6));
    @include mx--auto;

    &--margin {
      @include margin(40, bottom);
    }

    @include media(sm) {
      display: none !important;
      .b-form__row {
        display: none !important;
      }
    }
  }

  &__item {
    padding: rem(272) 0 rem(40);
    @include media(sm) {
      padding: rem(80) 0 rem(60);
    }
    @include media(sm) {
      height: 640px;
      padding-left: 12px;
      padding-right: 12px;
    }
  }

  &__policy {
    @include font(13);
    color: $lighten;
    max-width: 20%;
    margin: rem(20) auto;
    &--mobile {
      display: none;
    }
    a {
      text-decoration: underline !important;
      transition: .3s;

      &:hover {
        color: $accent;
      }
    }

    &--light {
      max-width: none;
      margin: 0;
    }

    @include media(sm) {
      display: none;
      &--mobile {
        display: block;
        max-width: none;
      }
    }
  }

  &__button {
    @include mx--auto;
    @include margin(60, bottom);

    &--margin {
      @include margin(30, bottom);
    }
    &--mobile {
      display: none;
    }

    @include media(sm) {
      display: none;
      &--mobile {
        display: flex;
        @include margin(40, bottom);
      }
    }
  }

  &__blocks {
    @include flex(false, center);
    @include margin(64, bottom);
    @include media(sm) {
      flex-wrap: wrap;
    }
    @include media(xs) {
      margin-bottom: 20px;
      display: block;
    }
  }

  &__block {
    @include size(rem(255), auto);
    @include margin(30, right);
    text-align: center;

    &:last-child {
      margin-right: 0;
    }
    @include media(sm) {
      margin: 0;
      width: 49%;
      height: 220px;
    }
    @include media(xs) {
      @include mx--auto;
      width: 100%;
      margin: 0;
    }
  }

  &__icon {
    @include size(rem(80));
    @include mx--auto;

    svg {
      max-width: none;
    }
    @include media(xs) {
      @include size(rem(60))
    }
  }

  &__text {
    @include font(15);
    @include margin(20, top);
    line-height: rem(30);
    letter-spacing: -.2px;

    span {
      @include font(15, $bold, true);
    }
    @include media(xs) {
      line-height: 25px;
    }
  }

  .swiper-button-next {
    right: rem(40);
  }

  .swiper-button-prev {
    left: rem(40);
  }
  .swiper-button-next, .swiper-button-prev {
    @include media(sm) {
      display: none;
    }
  }
  .select2-container {
    z-index: 11;
    position: relative;
  }

  @include media(sm) {
    text-align: center;
    max-height: rem(640);
    .swiper-slide {
      max-height: rem(450);
    }
  }
  @include media(xs) {
    .swiper-slide {
      max-height: 640px;
    }
    .swiper-pagination {
      bottom: 60px;
    }
  }
}
