.b-footer {
  padding: rem(20) 0;

  &__container {
    @include flex(center, space-between);
    @include media(sm) {
      flex-direction: column;
    }
  }

  &__copy {
    @include font(12, 600);
    @include media(sm) {
      order: 2;
    }
  }

  &__socials {
    @include flex;
    @include media(sm) {
      order: 0;
    }
  }

  &__social {
    color: $accent;
    @include margin(40, right);

    &:last-child {
      margin-right: 0;
    }
    @include media(sm) {
      @include font(24);
    }
  }

  &__info {
    @include font(12, 600);
    a {
      display: block;
      color: $accent;
    }
    @include media(sm) {
      @include margin-y(20);
      a {
        display: inline;
      }
    }
  }
  @include media(xs) {
    text-align: center;
  }
}
