.b-faq {
  padding: rem(92) 0 rem(81);
  color: $light;
  background: url("../img/faq/bg.jpg");
  &__title {
    @extend %title--big;
    @include margin(59, bottom);
    text-align: center;
  }
  &__item {
    @include margin(20, bottom);
    cursor: pointer;
    &:last-child {
      margin-bottom: 0;
    }
    &--active {
      .b-faq__header {
        &::before {
          width: 0;
        }
      }
      .b-faq__cross {
        span {
          background: $lighten;
          &:last-child {
            transform: translate(-50%, -50%) rotate(0deg);
          }
        }
      }
    }
  }
  &__header {
    @include flex(center, space-between);
    padding-bottom: rem(20);
    position: relative;
    &::before {
      @include pseudo;
      position: absolute;
      width: 100%;
      height: 1px;
      bottom: 0;
      left: 0;
      transition: .3s;
      background: $lighten;
    }
    transition: .3s;
  }
  &__question {
    @include font(20, 600);
    @include media(xs) {
      @include font(18);
      max-width: 90%;
    }
  }
  &__answer{
    @include font(16);
    line-height: rem(20);
    display: none;
  }
  &__cross {
    @include size(rem(26));
    position: relative;
    cursor: pointer;
    span {
      width: 100%;
      height: 2px;
      background: $accent;
      position: absolute;
      transition: .3s;
      &:first-child {
        top: 50%;
        transform: translateY(-50%);
      }
      &:last-child {
        left: 51%;
        top: 50%;
        transform-origin: top center;
        transform: translate(-50%) rotate(90deg);
      }
    }
    @include media(xs) {
      position: absolute;
      right: 0;
    }

  }
}
