.b-about {
  padding: rem(85) 0 rem(60);
  &__container {
    @include flex;
    @include media(sm) {
      display: block;
    }
  }
  &__block {
    @include margin(35, right);
  }
  &__title {
    @include font(50, $medium, true);
    font-family: $accentFont;
    color: $main;
    letter-spacing: -3px;
    @include media(sm) {
      @include font(30);
      line-height: rem(40);
      max-width: 350px;
      letter-spacing: normal;
    }
  }
  &__subtitle {
    @include font(18, 600);
    @include margin(28, top);
    display: block;
    letter-spacing: -.2px;
    @include media(sm) {
      @include margin(10, top);
      max-width: 350px;
    }
  }
  &__info {
    margin-left: rem(45);
    @include media(sm) {
      margin-left: 0;
      margin-top: rem(20);
    }
  }
  &__text {
    @include font(16);
    @include margin(20, bottom);
    line-height: rem(20);
    &:last-child {
      margin-bottom: 0;
    }
    @include media(sm) {
      margin-bottom: 0;
    }
  }
  &__button {
    display: none;
    @include media(sm) {
      display: flex;
      @include size(rem(150), rem(40));
      margin-top: rem(20);
      @include border(1, $accent);
      background: $light;
      span {
        @include font(14, 600);
        color: $accent;
      }
    }
  }
  @include media(sm) {
    padding: rem(70) 0 rem(47);

  }
}
