.b-ask {
  padding: rem(81) 0 rem(60);
  &__title {
    @extend %title--big;
    max-width: 80%;
    @include mx--auto;
    text-align: center;
    line-height: rem(50);
    @include media(xs) {
      max-width: none;
      @include font(30);
      line-height: 40px;
    }
  }
  &__content {
    @include flex;
    @include margin(30, top);
  }
  &__img {
    @include size(rem(285), rem(440));
    background: #efefef;
    position: relative;
    @include flex(flex-end);
    padding-bottom: rem(20);
    padding-left: rem(30);
    @include margin(95, right);
    img {
      position: absolute;
      @include size(rem(320), rem(360));
      left: rem(30);
      top: rem(-20);
      max-width: none;
    }
    @include media(sm) {
      display: none;
    }
  }
  &__name {
    @include font(20, 600);
    line-height: rem(27);
    color: $main;
  }
  &__work {
    @include font(14, 600);
    color: #a6a6a6;
    @include margin(5,top);
  }
  &__form {
    width: rem(730);
    max-width: 100%;
  }
  &__row {
    @include flex(center, space-between);
    @include margin(30, bottom);

    @include media(xs) {
      &:first-child {
        margin-bottom: 0;
        display: block;
      }
    }
  }
  &__input {
    @include border(1, $lighten);
    @include size(rem(350), rem(60));
    @include media(xs) {
      width: 100%;
      margin-bottom: 30px;
    }
  }
  &__policy {
    max-width: rem(350);
  }
  &__button {
    @include media(xs) {
      @include mx--auto;
      margin-top: 15px;
    }
  }
}
