.b-conditions {
  text-align: center;
  padding: rem(82) 0 rem(41);
  &__title {
    @extend %title--big;
    @include margin(18, bottom);
    @include media(xs) {
      font-size: 30px;
      line-height: 40px;
    }
  }

  &__subtitle {
    @include font(18, 600);
  }
  &__items {
    @include flex(center, space-between);
    @include margin(30, top);
    @include media(sm) {
      padding-bottom: rem(60);
      @include margin(40, bottom);
    }
    @include media(xs) {
      @include flex(flex-start, flex-start);
    }
  }
  &__item {
    text-align: center;
    width: rem(180);
    @include media(xs) {
      width: 100%;
    }
  }
  &__icon {
    svg {
      @include size(rem(40))
    }
  }
  &__text {
    @include font(14);
    text-align: center;
    line-height: rem(20);
    @include margin(20, top);
  }
  @include media(sm) {
    padding: rem(50) 0 rem(40);
  }
  @include media(xsm) {
    .swiper-pagination-bullet {
      width: 80px;
    }
  }
}
