.b-up {
  background: $accent;
  @include size(rem(60));
  @include flex(center, center);
  align-items: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.25);
  transition: .3s;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  right: rem(50);
  bottom: rem(50);
  @include z('dropdown');
  &--active {
    opacity: 1;
    visibility: visible;
  }
  i {
    @include font(32);
    line-height: rem(37);
    color: $light;
  }
  &:hover {
    opacity: .8;
  }
  @include media(sm) {
    display: none;
  }
}
