.b-documents {
  padding: rem(78) 0 rem(72);
  &__container {
    @include flex(false, space-between);
    @include media(sm) {
      display: block;
    }
  }
  &__col {
    @include margin(70, right);
    &:first-child {
      width: rem(404);
    }
    &:last-child {
      margin-right: 0;
    }
    @include media(sm) {
      width: 100% !important;
    }
  }
  &__title {
    @include font(50, $medium,true);
    line-height: rem(60);
    font-family: $mainFont;
    color: $main;
    @include media(sm) {
      @include font(30);
      line-height: normal;
      text-align: center;
    }
  }
  &__text {
    @include font(18, 600);
    line-height: rem(25);
    @include margin(20, top);
    @include media(sm) {
      text-align: center;
      @include font(16)
    }
  }
  &__items {
    display: block;
    @include media(sm) {
      @include flex;
    }
  }
  &__item {
    @include flex;
    @include margin(30, bottom);
    &:last-child {
      bottom: 0;
    }
    &--grey {
      color: #A6A6A6;
      svg {
        fill: #A6A6A6;
      }
    }
    @include media(sm) {
      display: block;
      text-align: center;
      margin-top: 20px;
    }
  }
  &__icon {
    @include size(rem(40));
    @include margin(12, right);
    @include media(sm) {
      @include mx--auto;
      margin-bottom: 10px;
    }
  }
  &__description {
    @include font(20);
    font-family: $accentFont;
    line-height: rem(23);
    @include media(sm) {
      display: block;
      width: 80%;
      @include mx--auto
    }
  }
  .swiper-container {
    @include media(xsm) {
      padding-bottom: 30px;
    }
  }
  .swiper-pagination {
    display: none;
    @include media(sm) {
      display: block;
      &-bullet {
        width: 60px;
      }
    }
    @include media(xsm) {
      &-bullet {
        width: 40px;
      }
    }
  }
}
