.swiper {
  &-pagination {
    &-bullet {
      @include size(rem(18));
      background: $lightGrey;
      @include border(2, transparent);
      @extend %circle;
      opacity: 1;
      &-active {
        transition: .3s;
        background: transparent;
        @include border(2, $lightGrey);
        @include media(sm) {
          border: none;
          background: $accent;
        }
      }
      @include media(sm) {
        @include size(rem(30), rem(3));
        border: none;
        border-radius: 0;
      }
    }
    &-line {
      display: none;
      @include media(sm) {
        display: block;
        text-align: center;
        bottom: 0 !important;
        position: absolute;
        @include z(dropdown);
        .swiper-pagination {
          &-bullet {
            height: 5px;
            width: 117px;
            background: $lighten;
            margin: 0 !important;
            &-active {
              background: #A6A6A6;
            }
          }
        }
      }
    }
  }
  &-button-next, &-button-prev {
    background: none;
    @include size(rem(19), rem(46));
    @include media(sm) {
      display: none;
    }
  }
  &-button-next {
    background: url("../img/right.png") no-repeat center center/cover;
  }
  &-button-prev {
    background: url("../img/left.png") no-repeat center center/cover;
  }

}
