.b-menu {
  @include flex;
  @include size(rem(540), auto);
  &__item {
    cursor: pointer;
    position: relative;
    @include margin(36, right);
    &:last-child {
      margin-right: 0;
    }
    &--active {
      position: static !important;
    }
    &:hover {
      color: $accent;
      .b-menu__icon svg {
        fill: $accent;
      }
      .b-submenu {
        opacity: 1;
        visibility: visible;
      }
    }
    @include media(sm) {
      padding-left: rem(23);
      @include flex(center, space-between);
      margin-right: 0;
      margin-bottom: rem(10);
      padding-bottom: rem(10);
      &:hover {
        color: $text;
        .b-menu__icon svg {
          fill: $text;
        }
        .b-submenu {
          opacity: 0;
          visibility: hidden;
        }
      }
      &--active {
        color: $accent;
        border-bottom: 1px solid #C4C4C4;
      }
    }
    @include media(xsm) {
      margin-bottom: rem(8);
    }
  }
  &__icon {
    position: absolute;
    @include font(21);
    bottom: rem(-28);
    left: 45%;
    transform: translateX(-50%);
    color: inherit;
    transition: .3s;
    svg {
      transition: .3s;
      fill: $text;
      @include size(rem(11), rem(6));
      @include media(sm) {
        @include size(rem(17), rem(24));
      }
    }
    @include media(sm) {
      transform: translateY(-50%) rotate(-90deg);
      transform-origin: center center;
      left: 90%;
      bottom: 0;
      top: 50%;
      @include size(rem(17), rem(24));
    }
  }
  &__link {
    @include font(16);
    color: inherit;
    transition: .3s;
    @include media(sm) {
      @include font(32, $medium, true);
      line-height: rem(70);
      font-family: $accentFont;
    }
  }
  @include media(sm) {
    display: block;
    width: 100%;
    margin-top: 35px;
    padding-left: 23px;
    position: relative;
  }

}
