.b-contacts {
  padding: rem(35) 0;
  @include border(1, $lighten, top);
  @include border(1, $lighten, bottom);
  max-height: rem(482);
  overflow: hidden;
  &__container {
    @include flex(false, space-between);
  }
  &__column {
    @include margin(50, right);
    width: 22%;
    &:last-child {
      margin-right: 0;
    }
    @include media(sm) {
      display: none;
      &--mobile {
        width: 100%;
        display: block;
      }
    }
    @include media(xsm) {
      margin-right: 0;
    }
  }
  &__items {
    @include media(sm) {
      @include flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  &__item {
    @include flex(center);
    @include margin(40, top);
    @include media(sm) {
      max-width: 50%;
    }
  }
  &__icon {
    @include font(14);
    line-height: rem(16);
    color: $accent;
    @include margin(20, right);
    @include media(xs) {
      margin-right: 10px;
    }
  }
  &__text {
    @include font(12, 600);
  }
  a.b-contacts__text {
    &:hover {
      color: $accent;
    }
  }
  &__info{
    span {
      display: block;
      @include font(12,600);
      color: #a6a6a6;
    }
  }
  &__link {
    @include font(14, $bold);
  }
  &__row {
    height: rem(240);
    @include margin(20,bottom);
  }
  &__title {
    display: block;
    @include font(16, $bold);
    @include margin(20, bottom);
  }
  &__nav {
    @include font(14);
    color: #a6a6a6;
    transition: .3s;
    @include margin(10, bottom);
    display: table;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      color: $accent;
    }
  }
}
