.b-submenu {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 320%;
  left: rem(-20);
  @extend %shadow;
  @include size(rem(350), rem(280));
  @include z('dropdown');
  background: $accent;
  color: $light;
  padding: rem(20) rem(23);
  transition: .3s;

  &::after {
    @include triangle($accent, up, 10);
    top: rem(-10);
    left: rem(34);
  }

  &--second {
    &::after {
      left: rem(45);
    }
  }

  &__item {
    position: relative;
    @include flex();
    color: $light;
    padding-left: rem(30);
    margin-bottom: rem(18);

    &--title {
      display: none;
    }

    &::before {
      @include pseudo();
      @include size(rem(15), 1px);
      background: $light;
      left: 0;
      top: rem(10);
      @include font(18);
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include media(sm) {
      color: $text;
      margin-bottom: rem(25);
      &--title {
        @include font(32, $bold, true);
        padding-bottom: rem(15);
        font-family: $accentFont;
        line-height: rem(70);
        @include flex(center);
        color: $accent;
        &::before {
          width: 115%;
          left: -10%;
          top: 100%;
          background-color: #C4C4C4;
        }
        .b-menu__icon {
          left: 0;
          top: 0;
          transform: rotate(90deg);
          svg {
            fill: $accent;
          }
        }
      }
      &::before {
        background: #363636;
      }
    }
    @include media(xsm) {
      margin-bottom: rem(12);
    }
  }

  &__link {
    letter-spacing: 1.11px;
    @include media(sm) {
      @include font(22);
      line-height: 25px;
    }
  }

  @include media(sm) {
    opacity: 1 !important;
    visibility: visible !important;
    position: absolute;
    display: none;
    //padding-top: 10%;
    top: 0;
    left: 15px;
    height: 100%;
    z-index: 12;
    width: 100%;
    background: $light;
    color: $text;
    box-shadow: none;
    &::after {
      display: none;
    }
  }
  @include media(xsm) {
    overflow: hidden;
    padding-bottom: rem(15);
    height: 100%;
  }
}
