// ÐŸÑÐµÐ²Ð´Ð¾ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹
@mixin pseudo($display: block, $pos: absolute, $content: '') {
  content: $content;
  display: $display;
  position: $pos;
}

// ÐžÑ‚ÑÑ‚ÑƒÐ¿Ñ‹
@mixin mx--auto {
  margin: {
    left: auto;
    right: auto;
  }
}


// Media
@mixin media($width, $type: max) {
  @if map_has_key($breakpoints, $width) {
    $width: map_get($breakpoints, $width);
    @if $type == max {
      $width: $width - 1px;
    }
    @media only screen and (#{$type}-width: $width) {
      @content;
    }
  }
}

// Aligne
@mixin align($position: absolute, $vertical: false, $horizontal: false) {
  @if $position {
    position: $position;
  }
  @if $vertical {
    top: 50%;
  }
  @if $horizontal {
    left: 50%;
  }

  @if $vertical and $horizontal {
    transform: translateX(-50%) translateY(-50%);
  } @else if $vertical {
    transform: translateY(-50%);
  } @else {
    transform: translateX(-50%);
  }
}


//Block Size
@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

//Border
@mixin border($size, $color,$direction: false) {
  @if ($direction) {
    border-#{$direction}: #{$size}px solid $color;

  } @else {
    border: #{$size}px solid $color;
  }
}


//Font
/** Function Rem **/
@function rem($pxsize) {
  @return ($pxsize/16)+rem;
}

@function em($pxsize) {
  @return ($pxsize/16)+em;
}

@mixin font($pxsize, $weight: false, $uppercase: false) {
  font-size: rem($pxsize);
  @if ($weight) {
    font-weight: $weight;
  }
  @if ($uppercase) {
    text-transform: uppercase;
  }
}
// Ð¢Ñ€ÐµÑƒÐ³Ð¾Ð»ÑŒÐ½Ð¸ÐºÐ¸
@mixin triangle($color, $direction, $size: 6, $position: absolute, $round: false) {
  @include pseudo($pos: $position);
  width: 0;
  height: 0;
  @if $round {
    border-radius: 3px;
  }
  @if $direction == down {
    border-left: rem($size) solid transparent;
    border-right: rem($size) solid transparent;
    border-top: rem($size) solid $color;
    margin-top: 0 - rem(round( $size / 2.5 ));
  } @else if $direction == up {
    border-left: rem($size) solid transparent;
    border-right: rem($size) solid transparent;
    border-bottom: rem($size) solid $color;
    margin-bottom: 0 - rem(round( $size / 2.5 ));
  } @else if $direction == right {
    border-top: rem($size) solid transparent;
    border-bottom: rem($size) solid transparent;
    border-left: rem($size) solid $color;
    margin-right: rem(-$size);
  } @else if $direction == left {
    border-top: rem($size) solid transparent;
    border-bottom: rem($size) solid transparent;
    border-right: rem($size) solid $color;
    margin-left: rem(-$size);
  }
}

// margin
@mixin margin($size, $direction) {
   @if ($direction) {
     margin-#{$direction}: rem($size);
   } @else {
     margin: rem($size);
   }
}
@mixin margin-x($size) {
     margin-left: rem($size);
     margin-right: rem($size);
}
@mixin margin-y($size) {
     margin-top: rem($size);
     margin-bottom: rem($size);
}
@mixin margin-xy($size...) {
     margin: rem($size);
}

//Overlay
@mixin overlay($pos) {
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: $pos;
  right: 0;
  top: 0;
}

//Flex
@mixin flex($vertical: false, $horizontal: false) {
  display: flex;
  @if ($vertical) {
    align-items: $vertical;
  }
  @if ($horizontal) {
    justify-content: $horizontal;
  }
}

// Z-index
@mixin z($z-index) {
  @each $name, $index in $z-indexes {
    @if $z-index == $name {
      z-index: $index;
    }
  }
}

/** EXTENDS **/

%circle {
  border-radius: 50%;
}

%uppercase {
  text-transform: uppercase;
}

%shadow {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
}

%title--big {
  @include font(40, $bold, true);
  font-family: $accentFont;
}

%title--medium {
  @include font(30, $bold, true);
  font-family: $accentFont;
}


