.b-form {
  &__title {
    @extend %title--medium;
    @include margin(25, bottom);
    letter-spacing: -.7px;
    text-align: left;
  }

  &__row {
    @include flex(false, center);

    &--center {
      @include flex(center, center);
    }

    &--between {
      @include flex(center, space-between);
    }
    @include media(xs) {
      display: block;
      &--center {
        display: flex;
      }
    }
  }

  &__input, &__select {
    border: none;
    color: #A6A6A6;
    @include size(rem(350), rem(60));
    @include font(14, 600);
    padding: rem(10) 0 rem(10) rem(20);
    @include border(1, $lighten, right);
    @include media(xsm) {
      width: 100%;
    }
  }

  &__input {
    &--small {
      width: rem(230);
      border: none;

      .select2-container {
        border: none;
      }
    }
  }

  &__textarea {
    @include size(100%, rem(200));
    @include border(1, $lighten);
    padding: rem(16);
    @include font(14, 600);
    color: #A6A6A6;
    max-width: 100%;
  }

  &__counter-title {
    @include font(20, $bold, true);
    @include margin(30, right);
    font-family: $accentFont;

  }

  &__counter-price {
    @extend %title--big;
    color: $main;
    font-family: $accentFont;
    position: relative;

    &::before {
      @include pseudo;
      background: $lighten;
      bottom: rem(-5);
      width: 100%;
      height: 1px;
    }

    @include media(sm) {
      @include font(30);
    }
  }

  &__range {
    @include size(rem(920), rem(10));
    margin: rem(30) auto rem(70);
    @include media(sm) {
      @include size(auto, rem(10));
    }
    @include media(xs) {
      margin: rem(30) rem(15) rem(70);
    }
  }

  &__column {
    @include size(rem(350), auto);
    text-align: left;

    &:first-child {
      @include size(rem(285), auto);
    }
  }

  &__upload {

  }

  &__drag {
    display: block;
    position: relative;
    @include size(100%, rem(40));
    @include font(14, 600);
    @include margin(20, top);
    color: #A6A6A6;
    padding: rem(10) rem(16);
    background: #EFEFEF;
    border: 1px dashed #c4c4c4;
    cursor: pointer;
    transition: .3s;

    input {
      position: absolute;
      z-index: -1;
      opacity: 0;
    }

    &--error {
      color: $accent;
      border: 1px dashed $accent;
    }

    &--hover {
      background: rgba(#EFEFEF, .8);
    }
  }
}
