.b-calculator {
  padding: rem(72) 0 rem(100);
  position: relative;
  text-align: center;
  &::before {
    @include pseudo;
    width: 100%;
    height: 50%;
    top: 0;
    left: 0;
    background: url("../img/calculator/bg.jpg") no-repeat center center/cover;
  }
  &--sales {
    padding: rem(92) 0 rem(60);
    &::before {
      background: url("../img/calculator/sales.jpg") no-repeat center center/cover;
    }
  }
  &--vikup {
    &::before {
      background: url("../img/calculator/vikup.jpg") no-repeat center center/cover;
    }
  }

  &__container {
    position: relative;
    @include media(xs) {
      padding: 0;
    }
  }

  &__title {
    @extend %title--big;
    @include margin(20, bottom);
    @include mx--auto;
    color: $light;
    letter-spacing: -.1px;
    line-height: rem(48);
    max-width: 60%;
    @include media(sm) {
      max-width: none;
      @include font(26);
      margin-bottom: rem(18);
    }
  }

  &__subtitle {
    @include font(16);
    color: $light;
    @include media(sm) {
      @include font(13);
    }
  }

  &__input, .select2-container {
    width: rem(285) !important;
    height: rem(40) !important;
    @include border(1, $lighten);
    @include margin(15, bottom);
    padding: 0 rem(15) !important;
  }
  &__form {
    @include margin(30, top);
    @extend %shadow;
    padding: rem(40) 0 rem(50);
    background: $light;
    &--sales {
      padding: rem(40) rem(30) rem(40) rem(40);
      @include margin(58, top);
      @include mx--auto;
      @include flex(false, space-between);
      @include size(rem(760), auto);
      max-width: 100%;
      @include media(xs) {
        flex-direction: column;
        .b-form__column {
          width: 100%;
        }
        &  .select2-container, .b-calculator__input {
          width: 100% !important;
        }
        .b-calculator__question {
          margin-top: 20px;
        }
        .b-calculator__line {
          display: block;
        }
      }
    }
    &--vikup {
      display: block;
    }
  }

  &__item {
    @include border(1, $lighten);
    @include size(50%, rem(320));
    transition: .3s;
    padding: rem(30) rem(55) rem(45);
    cursor: pointer;
    &--active {
      display: block !important;
      border: rem(6) solid $main;

      .b-calculator__icon svg {
        fill: $main;
      }
      .b-calculator__percent {
        color: $main;
      }
    }
    @include media(sm) {
      width: 100%;
      display: none;
    }
    @include media(xs) {
      height: auto;
      padding: rem(30) rem(12) rem(45);

    }
  }

  &__icon {
    @include margin(15, bottom);

    svg {
      max-width: rem(100);
      max-height: rem(56);
      transition: .3s;
      fill: #A6A6A6;
    }
  }
  &__name {
    @include font(24, $bold);
    font-family: $accentFont;
    @include margin(10, bottom);
    @include media(xs) {
      @include font(20);
    }
  }
  &__description {
    @include size(rem(235), auto);
    @include mx--auto;
  }
  &__info {
    @include margin(30, top);
  }
  &__text {
    @include font(12);
  }
  &__percent {
    @include font(34, $bold);
    transition: .3s;
    display: block;
    @include media(xs) {

    }
  }
  &__row {
    @include flex(center);
    @include margin(10, bottom);
    &:last-child {
      @include margin(0, bottom);
    }
    .b-calculator__text {
      @include size(rem(100), auto)
    }
  }
  &__rate {
    @include font(20, $bold);
    @include margin(20, left);
    text-align: left;
    display: block;
    &--sales {
      margin-left: 0;
      @include margin(15, bottom);
    }
    &--margin {
      @include margin(50, top);
      @include media(xs) {
        margin-top: 20px;
      }
    }
    @include media(xs) {
      margin-left: 0;
    }
  }
  &__line {
    @include flex(flex-start, space-between);
  }
  &__blocks {
    @include margin(20, top);
    padding: 0 rem(50);
    @include media(xs) {
      display: block;
    }
  }
  &__count {
    @include font(16);
  }
  &__current {
    @include font(26, $bold);
    display: block;
    &--active {
      color: $accent;
    }
  }
  &__result {
    text-align: left;
    display: block;
    @include font(14);
    font-family: $accentFont;
    line-height: rem(16);
    @include margin(11, bottom);
  }
  &__button {
    margin: rem(30) auto rem(0);
    &--sales {
      margin: rem(5) auto rem(0);
      @include media(xs) {
        display: none;
      }
    }
    &--mobile {
      display: none;
      @include media(xs) {
        display: block;
        margin-top: 20px;
      }
    }
  }
  &__question {
    @include font(20);
    font-family: $accentFont;
    @include margin(30, bottom);
    line-height: rem(20);
    display: block;
    text-align: left;
    &--margin {
      @include margin(10, bottom);
    }
  }
  &__checkbox {
    @include margin(20, bottom);
  }
  &__light,&__img-name  {
    display: block;
    text-align: left;
    color: #A6A6A6;
    @include font(14);
    font-family: $accentFont;
  }
  &__images {
    @include flex;
    text-align: left;
    @include margin(25, bottom);
  }
  &__checkbox {
    @include margin(15, bottom);
    &--margin {
      @include margin(5, bottom);
    }
  }
  &__img-name {
    padding-right: rem(5);
    padding-top: rem(5);
  }
  &__toggles {
    display: none;
    @include media(sm) {
      display: flex;
      z-index: 1;
    }
  }
  &__toggle {
    width: 50%;
    height: 40px;
    background: $light;
    border: 1px solid $lighten;
    &:hover {
      background: $main;
    }
    &--active {
      background: $main;
      border-color: $main;
      span {
        color: $light !important;
      }
    }
    span {
      color: #A6A6A6;
    }
  }
  .select2-container .select2-selection--single, .select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0;
  }
  @include media(sm) {
    padding: rem(70) 0 rem(30);
    .b-form__row {
      position: relative;
    }
  }
}
