.b-reviews {
  padding: rem(80) 0 rem(60);
  position: relative;
  &__title {
    @extend %title--big;
    text-align: center;
    max-width: 70%;
    @include mx--auto;
    @include media(xs) {
      max-width: none;
      @include font(30)
    }
  }
  &__slider {
    @include margin(30, top);
  }
  &__slide {
    width: auto !important;
    @include media(sm) {
      width: 50% !important;
      text-align: center;
    }
    @include media(xs) {
      width: 100% !important;
      padding-left: 12px;
      padding-right: 12px;
      text-align: left ;
      //margin-right: 20px;
    }
  }
  &__header {
    @include flex(center);
  }
  &__avatar {
    @include size(rem(80));
    @include margin(20, right);
  }
  &__name {
    @include font(20, 600);
    color: $main;
    line-height: rem(27);
    display: block;
  }
  &__car {
    @include font(14, 600);
    text-align: left;
  }
  &__block {
    padding: rem(15) rem(20);
    background-color: $light;
    max-width: rem(350);
    filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.2));
    @include margin(15, top);
    position: relative;
    &::before {
      @include triangle($light, up, 15);
      top: rem(-10);
      left: 7%;
    }
    @include media(xs) {
      max-width: none;
      min-height: 190px;
    }
    @include media(xs) {
      filter: none;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    }
  }
  &__text {
    @include font(14);
    line-height: rem(20);
  }
  &__button {
    margin: rem(40) auto 0;
  }
  .swiper-container {
    padding-bottom: rem(10);
    padding-left: rem(10);
    position: static;
    @include media(sm) {
      position: relative;
      padding-bottom: 53px;
    }
  }
  .swiper-button-next {
    right: 18%;
    top: 55%;
  }
  .swiper-button-prev {
    left: 18%;
    top: 55%;
  }
  .swiper-pagination {
    display: none;
    @include media(xs) {
      display: block;
      bottom: 0;
    }
  }
}
