.b-checkbox {
  display: flex;
  align-items: center;
  position: relative;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;

    &:checked + .b-checkbox__text::after {
      opacity: 1;
    }
    &:checked + .b-checkbox__text::before {
      border-color: $accent;
    }
  }

  &__text {
    @include font(12);
    position: relative;
    cursor: pointer;
    padding-left: rem(31);
    transition: all .3s;
    text-align: left;
    a {
      color: $accent;
    }
    &::before, &::after {
      @include pseudo;
      transition: all .3s;
    }
    &::before {
      @include size(rem(15));
      @include border(1, $lighten);
      background: $light;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &::after {
      position: absolute;
      @include size(rem(9));
      background: $accent;
      @include border(1, transparent);
      left: rem(3);
      top: 50%;
      transform: translateY(-50%);
      opacity: 0;
      transition: .3s;
    }

  }
  &--main {
    .b-checkbox__input:checked+.b-checkbox__text::before{
      border-color: $main;
      color: $main;
    }
    .b-checkbox__input:checked + .b-checkbox__text {
      color: $main;
    }
    .b-checkbox__text:after {
      background: $main;
    }
    .b-checkbox__text {
      @include font(15);
      font-family: $accentFont;
      line-height: rem(20);
      &::before {
        @include size(rem(20));
      }
      &::after {
        @include size(rem(15));
        border: 1px solid $main;
        left: rem(2.5);
      }
    }
  }
}
