.b-advantages {
  padding: rem(80) 0;
  background: $lightGrey;
  text-align: center;
  &--light {
    background: $light;
  }
  &__title {
    @extend %title--medium;
    @include margin(20, bottom);
    max-width: 60%;
    @include mx--auto;
    @include media(xs) {
      max-width: none;
    }
  }
  &__phone {
    color: $accent;
    @include font(60, $bold, true);
    font-family: $accentFont;
    &:hover {
      color: lighten($accent, 20);
    }
    @include media(xs) {
      @include font(46)
    }
  }
  &__items {
    @include flex(center, space-between);
    flex-wrap: wrap;
    @include margin(70, top);
    @include media(sm) {
      flex-wrap: nowrap;
      align-items: flex-start;
      justify-content: flex-start;
    }
    @include media(xs) {
      margin-top: 30px;
    }
  }
  &__item {
    @include flex(center);
    @include size(rem(465), rem(70));
    @include margin(42, bottom);
    @include media(sm) {
      display: block;
      text-align: center;
      height: auto;
      margin-bottom: 0;
    }

  }
  &__icon {
    @include margin(23, right);
    svg {
      max-width: none;
      @include size(rem(60));
    }
  }
  &__text {
    @include font(16);
    line-height: rem(25);
    text-align: left;
    span {
      text-transform: uppercase;
      font-weight: $bold;
    }
    @include media(sm) {
      text-align: center;
    }
    @include media(xs) {
      max-width: 300px;
      @include mx--auto;
      margin-top: 10px;
    }
  }
  @include media(sm) {
    background: $light;
    padding: rem(40) 0;
    .swiper-container {
      padding-bottom: 33px;
    }
  }
  @include media(xs) {
    padding-bottom: 0;

    .swiper-pagination-bullet {
        width: 50px;
    }
  }
}
